<template>
  <div>
    <div>
      <el-form size="mini" :model="form" inline>
        <el-form-item>
          <el-select v-model="searchType" placeholder="请选择" style="width: 100px">
            <el-option label="全部" value="0" />
            <el-option label="买家ID" value="userName" />
            <el-option label="订单编号" value="orderCode" />
            <el-option label="退款编号" value="refundCode" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-input v-model="searchInput" />
        </el-form-item>
        <el-form-item label="退款状态">
          <el-select v-model="form.orderStatus" placeholder="请选择" style="width: 120px">
            <el-option label="全部" value="" />
            <el-option label="退款完成" :value="9" />
            <el-option label="退款中" :value="4" />
          </el-select>
        </el-form-item>
        <el-form-item label="退款类型">
          <el-select v-model="form.serviceType" placeholder="请选择" style="width: 100px">
            <el-option label="全部" value="" />
            <el-option label="仅退款" :value="0" />
            <el-option label="退货退款" :value="1" />
          </el-select>
        </el-form-item>
        <el-form-item label="退款原因">
          <el-input v-model="form.refundCause" />
        </el-form-item>
        <el-form-item>
          <el-button class="inquire1" type="success" @click="onSearch">查询</el-button>
          <el-button @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-table :data="refundOrderList" border>
        <el-table-column prop="refundCode" label="退款编号" height="400px" align="center" />
        <el-table-column prop="orderCode" label="订单号" align="center" />
        <el-table-column prop="prodCode" label="商品编号" align="center" />
        <el-table-column label="商品信息" align="center" width="300">   <!-- show-overflow-tooltip -->
          <template slot-scope="scope">
            <div class="shopInfo_box">
              <span class="text">{{ scope.row.prodName }}</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="买家ID" align="center" />
        <el-table-column prop="refundTime" label="退款时间" align="center" show-overflow-tooltip />
        <el-table-column prop="refundCause" label="退款原因" align="center" show-overflow-tooltip />
        <el-table-column label="退款类型" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.serviceType === 0">仅退款</span>
            <span v-else-if="scope.row.serviceType === 1">退货退款</span>
          </template>
        </el-table-column>
        <el-table-column label="退款状态" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.orderStatus === 9" style="color: #666666; font-size: 12px">退款完成</span>
            <span v-else-if="scope.row.orderStatus === 4" style="color: #e20202; font-size: 12px">待处理</span>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RefundOrderList',

  data() {
    return {
      value: '',
      refundOrderList: [],
      pageNo: 1, // 分页当前分页
      total: 0, // 分页总条数
      form: {}, // 搜索用的表单
      searchType: '', // 搜索类型
      searchInput: '', // 搜索内容
    }
  },

  methods: {
    // 点击查看物流
    onExpress () {
      this.$router.push({ path: '/adminOrder/refundOrderList/express' })
    },
    // 分页
    handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      this.apiReturnStock()
    },
    // 点击搜索
    onSearch () {
      this.apiReturnStock()
    },
    // 点击重置
    onReset () {
      this.form = {}
      this.searchType = '' // 搜索类型
      this.searchInput = '' // 搜索内容
    },

    // api--------------------------------------------------------------------
    // 订单退货查询
    apiReturnStock () {
      let data = {
        pageNo: this.pageNo,
        ...this.form
      }
      // 模糊查询
      data[this.searchType] = this.searchInput
      this.$http.post('/woho/order/returnStock/query', data)
        .then((res) => {
          if (res.data.success) {
            this.refundOrderList = res.data.data.list
            this.total = res.data.data.total
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
  },

  created () {
    this.apiReturnStock()
  }
}
</script>

<style lang="scss" scoped>
  .shopInfo_box{
    display: flex;
    justify-content: space-around;
    .img{
      width: 70px;
      height: 70px;
    }
    .text{
      width: 200px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
  }
</style>
